// import { config } from "bluebird";
import { Promise } from "bluebird";
import CryptoJS from "crypto-js";
import { HmacSHA256 } from "crypto-js";
import url from "url";
import moment from "moment";

import { get, post } from "../httpClients/httpClients";

const URL = "https://api.huobipro.com";

const HOST = url.parse(URL).host;

const DEFAULT_HEADERS = {
  "Content-Type": "application/json",
  "User-Agent":
    "Mozilla/5.0 (Windows NT 6.1; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/39.0.2171.71 Safari/537.36",
};

const HUOBI = {
  access_key: process.env.VUE_APP_ACCESS_KEY,
  secretkey: process.env.VUE_APP_SECRETKEY,
  account_id: process.env.VUE_APP_ACCOUNT_ID,
  account_id_pro: process.env.VUE_APP_ACCOUNT_ID_PRO,
  trade_password: process.env.VUE_APP_TRADE_PASSWORD,
};

function getAuth() {
  var sign = HUOBI.trade_password + "hello, lewin";
  var md5 = CryptoJS.MD5(sign).toString().toLowerCase();
  let ret = encodeURIComponent(
    JSON.stringify({
      assetPwd: md5,
    })
  );
  return ret;
}

function signSHA(method, baseURL, path, data) {
  var pars = [];
  for (let item in data) {
    pars.push(item + "=" + encodeURIComponent(data[item]));
  }
  var p = pars.sort().join("&");
  var meta = [method, baseURL, path, p].join("\n");
  var hash = HmacSHA256(meta, HUOBI.secretkey);
  var signature = encodeURIComponent(CryptoJS.enc.Base64.stringify(hash));
  p += `&Signature=${signature}`;

  return p;
}

function getBody() {
  return {
    AccessKeyId: HUOBI.access_key,
    SignatureMethod: "HmacSHA256",
    SignatureVersion: 2,
    Timestamp: moment.utc().format("YYYY-MM-DDTHH:mm:ss"),
  };
}

function callApi(method, path, payload, body) {
  return new Promise((resolve) => {
    var url = `${URL}${path}?${payload}`;
    var headers = DEFAULT_HEADERS;
    headers.AuthData = getAuth();

    if (method === "GET") {
      get(url, {
        timeout: 1000,
        headers: headers,
      })
        .then((data) => {
          let json = JSON.parse(data);
          if (json.status === "ok") {
            resolve(json.data);
          } else {
            resolve(null);
          }
        })
        .catch(() => {
          resolve(null);
        });
    } else if (method === "POST") {
      post(url, body, {
        timeout: 1000,
        headers: headers,
      })
        .then((data) => {
          let json = JSON.parse(data);
          if (json.status === "ok") {
            // console.log(json.data);
            resolve(json.data);
          } else {
            resolve(null);
          }
        })
        .catch(() => {
          resolve(null);
        });
    }
  });
}

export function getAccount() {
  var path = `/v1/account/accounts`;
  var body = getBody();
  var payload = signSHA("GET", HOST, path, body);
  return callApi("GET", path, payload, body);
}

export function getBalance() {
  var account_id = HUOBI.account_id_pro;
  var path = `/v1/account/accounts/${account_id}/balance`;
  var body = getBody();
  var payload = signSHA("GET", HOST, path, body);
  return callApi("GET", path, payload, body);
}

export function buyLimit(symbol, amount, price, source) {
  var path = "/v1/order/orders/place";
  var body = getBody();
  var payload = signSHA("POST", HOST, path, body);

  body["account-id"] = HUOBI.account_id_pro;
  body.source = source;
  body.type = "buy-limit";
  body.amount = amount;
  body.symbol = symbol;
  body.price = price;

  return callApi("POST", path, payload, body);
}

export function buyMarket(symbol, amount, source) {
  var path = "/v1/order/orders/place";
  var body = getBody();
  var payload = signSHA("POST", HOST, path, body);

  body["account-id"] = HUOBI.account_id_pro;
  body.source = source;
  body.type = "buy-market";
  body.amount = amount;
  body.symbol = symbol;

  return callApi("POST", path, payload, body);
}

export function getAllOpenOrder(symbol, side) {
  var path = "/v1/order/openOrders";
  var body = getBody();
  var payload = signSHA("GET", HOST, path, body);

  body["acoount-id"] = HUOBI.account_id_pro;
  body.symbol = symbol;
  body.side = side;

  return callApi("GET", path, payload, body);
}
