<template>
  <b-overlay
    :show="overlay.show"
    :variant="overlay.variant"
    :opacity="overlay.opacity"
    :blur="overlay.blur"
    :rounded="overlay.sm"
  >
    <template #overlay>
      <div class="text-center">
        <b-icon icon="arrow-clockwise" font-scale="3" animation="spin" />
        <p style="margin-top: 10px">Loading...</p>
      </div>
    </template>

    <div class="container" style="margin-top: 10px; margin-bottom: 10px">
      <h1 style="text-align: center">GET ACCOUNT INFO</h1>

      <div class="card" style="margin-bottom: 10px">
        <div class="card-body">
          <p><span>Total Account Pro:</span> {{ AccountInfo.length }}</p>

          <div class="row">
            <b-table
              striped
              hover
              :items="AccountInfo"
              :fields="fieldAccountInfo"
            >
              <template #empty="">
                <span>No data</span>
              </template>
            </b-table>
          </div>
        </div>
      </div>

      <h1 style="text-align: center">GET BALANCE ACCOUNT</h1>

      <div class="card">
        <div class="card-body">
          <div class="col">
            <div class="row">
              <p><span>Account ID PRO:</span> {{ BalanceAccount["id"] }}</p>
            </div>
            <div class="row">
              <p><span>Type:</span> {{ BalanceAccount["type"] }}</p>
            </div>
            <div class="row">
              <p><span>State:</span> {{ BalanceAccount["state"] }}</p>
            </div>

            <div class="row">
              <div class="col log-session">
                <b-table
                  sticky-header=""
                  striped
                  :fields="fieldLogsesion"
                  :items="listLogession"
                >
                  <template #empty="">
                    <span>No log</span>
                  </template>
                </b-table>
              </div>
            </div>

            <div class="row" v-for="(buyMany, index) in buyMany" :key="index">
              <div class="col" cols="2" cols-sm="2" cols-md="2" cols-lg="3">
                <b-form-input
                  v-model="buy.symbol"
                  placeholder="Symbol"
                  :disabled="overlay.show"
                ></b-form-input>
              </div>

              <div class="col" cols="2" cols-sm="2" cols-md="2" cols-lg="3">
                <b-form-input
                  v-model="buyMany.amount"
                  type="number"
                  placeholder="Amount"
                  :disabled="overlay.show"
                ></b-form-input>
              </div>

              <div class="col" cols="2" cols-sm="2" cols-md="2" cols-lg="3">
                <b-form-input
                  v-model="buyMany.price"
                  type="number"
                  placeholder="Price"
                  :disabled="overlay.show"
                ></b-form-input>
              </div>

              <div class="col" cols="2" cols-sm="2" cols-md="2" cols-lg="3">
                <b-form-input
                  v-model="buy.source"
                  type="text"
                  placeholder="Source"
                  :disabled="overlay.show"
                ></b-form-input>
              </div>

              <div class="col" cols="4" cols-sm="4" cols-md="4" cols-lg="3">
                <b-button
                  style="background-color: #f54242; border: none"
                  @click="handleDoDelete(index)"
                  :disabled="overlay.show"
                  >X</b-button
                >
              </div>
            </div>

            <div class="row">
              <div class="col">
                <b-button
                  @click="doRepeatBuyLimit(buy)"
                  style="
                    margin-top: 10px;
                    margin-bottom: 10px;
                    min-width: 110px;
                    text-float: right;
                  "
                  :disabled="overlay.show"
                  >Buy Limit</b-button
                >
              </div>
              <div class="col text-right">
                <b-button
                  @click="stopBuy()"
                  style="
                    margin-top: 10px;
                    margin-bottom: 10px;
                    min-width: 110px;
                    text-float: left;
                  "
                  :disabled="overlay.show"
                  >Stop Buy</b-button
                >
              </div>
              <div class="col text-right">
                <b-button
                  @click="handleAddBuyMany()"
                  style="
                    margin-top: 10px;
                    margin-bottom: 10px;
                    min-width: 110px;
                    text-float: left;
                  "
                  :disabled="overlay.show"
                  >+</b-button
                >
              </div>
              <div class="col text-right">
                <b-button
                  @click="doBuyMarket(buy)"
                  style="
                    margin-top: 10px;
                    margin-bottom: 10px;
                    min-width: 110px;
                    text-float: left;
                  "
                  :disabled="overlay.show"
                  >Buy Market</b-button
                >
              </div>
            </div>

            <div class="row">
              <b-table
                striped
                hover
                sticky-header
                :items="BalanceAccount['list']"
                :fields="fieldsBalanceAccount"
              >
                <template #empty="">
                  <span>No data</span>
                </template>
              </b-table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </b-overlay>
</template>

<script>
import {
  buyLimit,
  buyMarket,
  getAccount,
  getAllOpenOrder,
  getBalance,
} from "../api/hbsdk";
import { MakeToast } from "../utils/MakeToast";
import { convertNumber } from "../utils/convertNumber";
export default {
  name: "Trade",
  data() {
    return {
      clear: null,
      AccountInfo: [],
      BalanceAccount: {},
      listOrderOpen: {},
      buy: {
        symbol: "",
        amount: "",
        price: "",
        source: "spot-api",
      },
      buyMany: [
        {
          price: "",
          amount: "",
        },
      ],
      openOrder: {
        symbol: "",
        side: "",
      },
      overlay: {
        show: false,
        variant: "light",
        opacity: 1,
        blur: "1rem",
        rounded: "sm",
      },
      listLogession: [],
    };
  },
  created() {
    this.initData();
  },

  computed: {
    fieldAccountInfo() {
      return [
        {
          key: "id",
          sortable: true,
        },
        {
          key: "state",
          sortable: true,
        },
        {
          key: "subtype",
          sortable: true,
        },
        {
          key: "type",
          sortable: true,
        },
      ];
    },
    fieldsBalanceAccount() {
      return [
        {
          key: "balance",
          sortable: true,
        },
        {
          key: "currency",
          sortable: true,
        },
        {
          key: "seq-num",
          sortable: true,
        },
        {
          key: "type",
          sortable: true,
        },
      ];
    },
    fieldLogsesion() {
      return [
        {
          logession: "Log Session",
          sortable: true,
        },
      ];
    },
  },
  methods: {
    async initData() {
      this.overlay.show = true;
      await this.getAccountInfor();
      await this.getBalanceAccount();
      await this.getAllOrder(this.openOrder);
      this.overlay.show = false;
    },
    handleAddBuyMany() {
      const buyManyBy = {
        price: "",
        amount: "",
      };
      this.buyMany.push(buyManyBy);
    },
    handleDoDelete(index) {
      this.buyMany.splice(index, 1);
    },
    async getAllOrder(openOrder) {
      const symbol = this.buy.symbol;
      const side = openOrder.side;
      await getAllOpenOrder(symbol, side).then((res) => {
        this.listOrderOpen = res;
        for (let item = 0; item < this.listOrderOpen.length; item++) {
          var ts = this.listOrderOpen[item]["created-at"];
          var accountID = this.listOrderOpen[item]["account-id"];
          var amount = convertNumber(this.listOrderOpen[item].amount);
          var symbol = this.listOrderOpen[item].symbol;
          var price = convertNumber(this.listOrderOpen[item].price);
          var typeOrder = this.listOrderOpen[item].type;

          var date = new Date(ts).toLocaleDateString();
          var h = new Date(ts).getHours();
          var m = new Date(ts).getMinutes();
          var s = new Date(ts).getSeconds();
          var mls = new Date(ts).getMilliseconds();
          var time = h + ":" + m + ":" + s + ":" + mls;
          const newLog = {
            logession: `AcountID: ${accountID} |  Amount: ${amount} | Symbol: ${symbol.toUpperCase()} | Type of Order: ${typeOrder} | Price: ${price} | Time: ${time} | Order at ${date} `,
          };
          this.listLogession.push(newLog);
        }
      });
    },
    async getAccountInfor() {
      // console.log("GET ACCOUNT INFO");

      await getAccount().then((res) => {
        // console.log(res);
        this.AccountInfo = res;
      });
    },

    async getBalanceAccount() {
      // console.log("GET BALANCE ACCOUNT");

      await getBalance()
        .then((res) => {
          // console.log(res);
          this.BalanceAccount = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    async doBuyLimit(buy) {
      // var date = new Date().toLocaleDateString();
      // var h = new Date().getHours();
      // var m = new Date().getMinutes();
      // var s = new Date().getSeconds();
      // var mls = new Date().getMilliseconds();
      // var time = h + ":" + m + ":" + s + ":" + mls;
      // console.log(this.buyMany.length);
      for (let i = 0; i < this.buyMany.length; i++) {
        const symbol = buy.symbol;
        const amount = this.buyMany[i].amount;
        const price = this.buyMany[i].price;
        const source = buy.source;
        await buyLimit(symbol, amount, price, source);
      }

      // if (
      //   source !== "spot-api" ||
      //   symbol === "" ||
      //   amount === "" ||
      //   price === ""
      // ) {
      //   MakeToast({
      //     variant: "warning",
      //     title: "Warning",
      //     content: "Require Field",
      //   });
      // } else {
      // await buyLimit(symbol, amount, price, source);
      // .then((res) => {
      //   console.log(res);
      //   if (res !== 0) {
      //     const newLog = {
      //       logession: `Submited at |  Time: ${time} | Date at ${date} | Buy Successfully `,
      //     };
      //     this.listLogession.push(newLog);
      //   } else {
      //     const newLog = {
      //       logession: `Submited at |  Time: ${time} | Date at ${date} | Buy Fail `,
      //     };
      //     this.listLogession.push(newLog);
      //   }
      // });
      // buy.symbol = "";
      // buy.amount = "";
      // buy.price = "";
      // MakeToast({
      //   variant: "success",
      //   title: "Success",
      //   content: `You buy limit success symbol ${symbol} by ${price} `,
      // });
      await this.getBalanceAccount();
      await this.getAllOrder(this.openOrder);
      // }
      this.overlay.show = false;
    },
    async doRepeatBuyLimit(buy) {
      var timeOut = 5;
      this.clear = setInterval(async () => {
        for (let i = 0; i < this.buyMany.length; i++) {
          const symbol = buy.symbol;
          const amount = this.buyMany[i].amount;
          const price = this.buyMany[i].price;
          const source = buy.source;

          var buyManyType = {
            symbol: symbol,
            amount: amount,
            price: price,
            source: source,
          };
          await this.doBuyLimit(buyManyType);
        }
      }, timeOut);
    },
    stopBuy() {
      console.log("STOP STOP STOP");
      clearInterval(this.clear);
    },
    async doBuyMarket(buy) {
      this.overlay.show = true;
      const symbol = buy.symbol;
      const amount = buy.amount;
      const source = buy.source;

      await buyMarket(symbol, amount, source);

      MakeToast({
        variant: "success",
        title: "Success",
        content: `You buy market success symbol ${symbol} `,
      });
      await this.getBalanceAccount();
      await this.getAllOrder(this.openOrder);
      this.overlay.show = false;
    },
  },
};
</script>

<style lang="scss" scoped>
p {
  span {
    font-weight: 600;
  }

  text-transform: capitalize;
}
.b-table-sticky-header {
  width: 100%;
}
</style>
